import {makeObservable, observable, action, reaction} from 'mobx';
import knexInstance from 'knex';
import {KNEX_CLIENTS} from '@utils';
import * as sqlFormatter from 'sql-formatter';

class EditorStore {
  SMALL_WIDTH = 700;

  containerRef;
  isSmall = false;

  code = '// knex code\nknex("table").select()\n';
  sql;

  client = KNEX_CLIENTS[0];
  knex;

  disposers = [];

  constructor() {
    makeObservable(this, {
      containerRef: observable,
      code: observable,
      sql: observable,
      client: observable,
      knex: observable,

      setRef: action,
      setCode: action,
      setSql: action,
      setClient: action,
      setKnex: action
    });

    this.resizeObserver = new ResizeObserver(this.calculate);

    this.setKnex(knexInstance({client: this.client.id}));

    this.disposers = [
      reaction(
        () => this.containerRef,
        () => this.resizeObserver.observe(this.containerRef)
      ),
      reaction(
        () => [this.code, this.client],
        () => this.onCodeChange()
      )
    ];

    this.onCodeChange();
  }

  setRef = (ref) => {
    this.containerRef = ref;
  };

  setIsSmall = (state) => {
    this.isSmall = state;
  };

  setCode = (code) => {
    this.code = code;
  };

  setSql = (sql) => {
    this.sql = sql;
  };

  setClient = ({target}) => {
    const client = KNEX_CLIENTS.find(({id}) => id === target.value) ?? this.client;

    this.client = client;

    this.setKnex(knexInstance({client: client.id}));
  };

  setKnex = (knex) => {
    this.knex = knex;
  };

  calculate = (entry) => {
    const [{target: container} = {}] = entry || [];

    if (!container) {
      return;
    }

    const containerWidth = container.offsetWidth;

    if (containerWidth >= this.SMALL_WIDTH) {
      return this.setIsSmall(false);
    }

    return this.setIsSmall(true);
  };

  onCodeChange() {
    // кажется, что не используется, но на самом деле используется в eval
    const {knex} = this;

    try {
      let sql = '--- generated SQL code\n';
      let code = eval(this.code).toQuery();

      try {
        code = sqlFormatter.format(code, {language: this.client.formatter});
      } catch(err) {
        sql += `--- sqlFormatter failed to run: ${err?.toString() ?? err}\n`;
      }

      this.setSql(`${sql}${code}\n`);
    } catch(err) {
      this.setSql(`--- ${err?.toString() ?? err}\n`);
    }
  }
}

export default EditorStore;